import {fetchImageThumbnails} from './api';
import {put, take, fork} from 'redux-saga/effects';

export function* loadImages() {
  console.log('loading images from flickr...');
  try {
    const images = yield fetchImageThumbnails();
    yield put({type: 'IMAGES_LOADED', images});
  } catch(e) {
    yield put({type: 'IMAGE_LOAD_FAILURE', message: e.message});
  }
}

export function* watchForLoadImages() {
  while(true){
    yield take('LOAD_IMAGES'); // waiting for LOAD_IMAGES action
    yield fork(loadImages); // fork helps convert to non-blocking saga - can be executed even when the previous call is in progress
  }
}



