const API_KEY = 'c6723d0bccc3fe574a4f49364f7b5dba';
const API_ENDPOINT = `https://api.flickr.com/services/rest/?method=flickr.photosets.getPhotos&api_key=${API_KEY}&photoset_id=72157682809279776&user_id=154039829%40N08&format=json&nojsoncallback=1`;

export const fetchImageThumbnails = () => {
  return fetch(API_ENDPOINT).then(response => {
    return response.json().then(json => {
      return json.photoset.photo.map(({farm, server, id, secret}) => `https://farm${farm}.staticflickr.com/${server}/${id}_${secret}_n.jpg`
      );
    })
  })
};



